import React from "react";

import globalStyles from "../styles.module.scss";
import styles from "./_hero.module.scss";
import bgHeroVideo from "../../assets/video/video.mp4";
const Hero = () => {
  return (
    <section>
      <div className={styles.videoWrap}>
        <div className={styles.overlay}>
          <h1 className={styles.content}>
            <span className={styles.curlySpan}> Welcome</span>
            <span className={styles.equalSpan}>to my portfolio</span>
          </h1>
          <video src={bgHeroVideo} autoPlay loop muted />
        </div>
      </div>

      <div className={globalStyles.container}>
        <div className={styles.descWrap}>
          <p className={styles.heroDesc}>
            Hi everyone! My name is Vlad, I'm a Full stack developer. Below
            you'll find more information about projects I've been involved in
            and my tech stack
          </p>
        </div>
      </div>
    </section>
  );
};

export default Hero;
