import React from "react";
import styles from "./_resume.module.scss";
import resume from "../../assets/documents/Vlad_Fominov_junior_Full_Stack.pdf";
import globalStyles from "../styles.module.scss";

function Resume() {
  return (
    <section section className={styles.section}>
      <div className={globalStyles.container}>
        <h2 section className={styles.resumeTitle}>
          Check my CV
        </h2>
        <a
          className={styles.linkButton}
          href={resume}
          role="button"
          download="Vlad_Fominov_junior_Full_Stack.pdf"
        >
          Download CV
        </a>
      </div>
    </section>
  );
}

export default Resume;
